import React, { PureComponent, Suspense } from "react";
import { managePostHeight } from '../actions/themeActions';
import { connect } from 'react-redux';
import { rID, getCurrentTheme, IS_THEME } from '../utils'
import { CloudHostUrl, TagboxUpload } from '../constants'
import { suiteTrack, isTagEmbed, isTagShop, isTagbox, isSocialWall, isSocialEmbed, isEmbed } from "../actions/api";
import { spotlightFromScoket } from '../actions/displayAction'
const Header = React.lazy(() => import('./Header'));
const ReviewHeader = React.lazy(() => import('./ReviewHeader'));
const Ticker = React.lazy(() => import('./Components/Tickers'));
const Filters = React.lazy(() => import('./Components/Filters'));
const ThemeRoute = React.lazy(() => import('./Themes'));
const OnsiteUploadButton = React.lazy(() => import('./Components/UserComponents/OnsiteUploadButton'));
const TikTok_Img_update = React.lazy(() => import('./Components/customFunction/tikTok_Img_update'));


class App extends PureComponent {
  constructor() {
    super();
    this.state = {
      containerSize: {
        width: null,
        height: null,
      },
      parentID: ''
    };
    this.containerRef = React.createRef();
  }

  componentDidMount() {
    getCurrentTheme(this.props.wall.Personalization.widgetTheme)
    window.addEventListener('resize', this.handleResize);
    if (this.containerRef.current) {
      this.setState({
        containerSize: {
          width: this.containerRef.current.offsetWidth,
          height: this.containerRef.current.offsetHeight,
        }
      })
    }
    this.onLoadSpotLight()
  }
  componentWillUnmount() {
    window.removeEventListener('resize', this.handleResize);
  }

  handleResize = () => {
    if (this.containerRef.current) {
      this.setState({
        containerSize: {
          width: this.containerRef.current.offsetWidth,
          height: this.containerRef.current.offsetHeight,
        }
      })
    }
  };

  onLoadSpotLight = () => {
    if (isSocialWall && !isEmbed) {
      const { postData } = this.props
      if (postData.completeDataObject && Object.keys(postData.completeDataObject).length > 0) {
        var spotLightData = null
        Object.entries(postData.completeDataObject).forEach(([key, value]) => {
          if (value?.highlight == 1 && spotLightData == null) {
            spotLightData = value
          }
        });
        if (spotLightData && Object.keys(spotLightData).length > 0) spotlightFromScoket(spotLightData)

      }
    }
  }

  componentDidUpdate() {
    let p_class = document.querySelector('.tb_app_container')
    let P_id = rID ? rID : p_class?.parentNode?.id
    this.setState({ parentID: P_id })
  }
  render() {
    const { wall, reviewFilters, webFilters, postData, languageSetting, filter_tags, spotlight } = this.props;
    const headerPositionTop = wall.Banner && Object.keys(wall.Banner).length > 0 && ((wall.Banner.status === 1 && wall.Banner.banner_position === 1) || wall.BannerImage.status === 1 || wall.Banner.banner_position === "1") ? true : false;
    const headerPositionBottom = wall.Banner && Object.keys(wall.Banner).length > 0 && ((wall.Banner.status === 1 && wall.Banner.banner_position === 2) || wall.Banner.banner_position === "2") ? true : false;
    const filters = isTagEmbed ? (![55, 60, 16, 47, 57].includes(wall.Personalization.widgetTheme)) ? true : false : (![55, 52, 60, 16, 47, 57, 61].includes(wall.Personalization.widgetTheme)) ? true : false;
    const isReviewBanner = !isSocialWall && wall.Banner && Object.keys(wall.Banner).length > 0 && wall.Banner.status === 1 && wall.Banner.bannerType == 6 && wall.Personalization.filterStatus === 0 && wall.Personalization.widgetTheme != 57 ? true : false
    const web_Filter_Status = isTagEmbed ? wall.Personalization.filterStatus === 1 ? true : false : webFilters && webFilters.length > 1 ? true : false
    const teg_Filter_Status = filter_tags && filter_tags.length > 0 ? true : false
    const spotlightStatus = isSocialWall && spotlight.status ? true : false

    let brandNet = webFilters != '' && webFilters && webFilters.length > 0 ? webFilters.map(({ Network }) => Network.id) : null;
    if (!isTagShop && wall?.Background?.image && wall.Background.image.includes(CloudHostUrl)) wall.Background['image'] = String(wall.Background.image).replace(TagboxUpload, "");

    const backgroundStyle = wall?.Background?.transparent === 0 || (wall?.Background?.image && isTagEmbed) ? {
      backgroundImage: (wall?.Background?.image) ? `url(${wall.Background.image})` : '',
      backgroundColor: wall?.Background?.color
    } : null
    return <div className={`tb_app_container ${spotlightStatus ? 'tb_spotlight_ac' : ''}`}
      style={{ minHeight: isEmbed ? [47, 16, 49, 55, 60, 52].includes(wall.Personalization.widgetTheme) ? '100%' : `${this.props.postHeight}px` : null }}
      ref={this.containerRef}>
      {isSocialWall && !isSocialEmbed && wall.CustomTicker?.status === 1 && wall.CustomTicker.ticker_position == 1 ? <Ticker CustomTicker={wall.CustomTicker} UgcSettings={wall.UgcSettings} /> : null}
      {headerPositionTop && !spotlightStatus ? <Suspense fallback={<></>}> <Header Banner={wall.Banner} BannerImage={wall.BannerImage} LogoImage={wall.LogoImage} /></Suspense> : ''}
      <div className="tb_app_wrapper" style={backgroundStyle}>
        {isReviewBanner && headerPositionTop ? <Suspense fallback={<></>}><ReviewHeader reviewFilters={reviewFilters} appendData={postData.appendData} wall={wall} languageSetting={languageSetting} postData={postData} /></Suspense> : null}
        {wall.Personalization.widgetTheme!='88' ? (wall?.UserRule?.on_site_upload && wall.UgcSettings.onsite_status) || (suiteTrack && wall.UgcSettings.onsite_status) && (isTagbox || (isTagShop && !IS_THEME(50))) ? <Suspense fallback={<></>}> <OnsiteUploadButton UgcSettings={wall.UgcSettings} id={wall.Wall.id} tagShopWallId={wall.Wall.wallId} /> </Suspense> : null :null}
        {!isSocialWall && filters && (web_Filter_Status || teg_Filter_Status) ? <Suspense fallback={<></>}> <Filters type={wall.Personalization.filter_type} webFilters={webFilters} filter_tags={filter_tags} appendData={postData.appendData} wall={wall} languageSetting={languageSetting} postData={postData} /></Suspense>
          : ''}
        {!spotlightStatus ? <Suspense fallback={<></>}> <ThemeRoute {...this.props} brandingNetworks={brandNet} containerSize={this.state.containerSize} parentID={this.state.parentID} /></Suspense> : null}
        {/* {postData && postData.completeDataObject ? <Suspense fallback={<></>}> <TikTok_Img_update completeDataObject={postData.completeDataObject} postData={postData.postData} /></Suspense> : ''} */}
        {isReviewBanner && headerPositionBottom ? <Suspense fallback={<></>}><ReviewHeader reviewFilters={reviewFilters} appendData={postData.appendData} wall={wall} languageSetting={languageSetting} postData={postData} /></Suspense> : null}
      </div>
      {headerPositionBottom && !spotlightStatus ? <Suspense fallback={<></>}> <Header Banner={wall.Banner} BannerImage={wall.BannerImage} LogoImage={wall.LogoImage} /></Suspense> : ''}
      {isSocialWall && !isSocialEmbed && wall.CustomTicker?.status === 1 && wall.CustomTicker.ticker_position == 2 ? <Ticker CustomTicker={wall.CustomTicker} UgcSettings={wall.UgcSettings} /> : null}
    </div>
  }
}
const mapStateToProps = state => {
  if (state.postHeight.manage_height === true) {
    let postHeight = state.postHeight.data.replace("px", "");
    return {
      postHeight,
      renderId: state.renderId
    }
  }
  else return { renderId: state.renderId }
}


const mapDispatchToProps = dispatch => {
  return {
    managePostHeight: (data) => dispatch(managePostHeight(data))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(App);
